.off_main{
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background: #000;
  position: relative;
  border: 20px solid #222;
  border-radius: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  .off_time{
    display: flex;
    flex-direction: column;
    align-items: center; 
    border: 1px solid red;
    margin-top: 7rem;
    p{
      color: #fff;
      font-size: 5rem;
      font-weight: bold;
    }
    .day{
      color: #fff;
      font-size: 1.4rem;
    }
  }
  .load {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid red;
    /*change these sizes to fit into your project*/
    width: 100px;
    height: 100px;
    span{
      border: 0;
      margin: 0;
      width: 40%;
      height: 40%;
      position: absolute;
      border-radius: 50%;
      animation: spin 2s ease infinite;
      &:first-child {
        background: #19a68c;
        animation-delay: -1.5s;
      }
      &:nth-child(2) {
        background: #f63d3a;
        animation-delay: -1s;
      }
      &:nth-child(3) {
        background: #fda543;
        animation-delay: -0.5s;
      }
      &:last-child {
        background: #fff;
      }
      
      @keyframes spin {
        0%,
        100% {
          transform: translate(0);
        }
        25% {
          transform: translate(160%);
        }
        50% {
          transform: translate(160%, 160%);
        }
        75% {
          transform: translate(0, 160%);
        }
      }
    }
    
  }

  

  .off_text{
    display: flex;
    flex-direction: column;
    text-align: center;
    color: #fff;
    border: 1px solid red;
    line-height: 1.3;
    margin-top: 11rem;
    h3{
      font-weight: bold;
      font-size: 5rem;
      margin-bottom: 1rem;
    }
    p{
      font-weight: bold;
      font-size: 3rem;
    }
  }
  .off_call{
    margin-top: 2rem;
    color: #000;
    font-weight: bold;
    width: 5%;
    border: 1px solid red;
    font-size: 1rem;
    padding: 1rem;
    text-align: center;
    align-self: center;
    border-radius: 10px;
    background: #f0f0f0;
    box-shadow:  0 0 0 #cccccc,
                   0 0 0 #ffffff,
                    10px 10px 10px #cccccc inset,
                    -10px -10px 10px #ffffff inset;
  }
  

}
@media (max-width: 768px) {
  .off_main{
    .off_time{
      margin-top: 7rem;
      p{
        font-size: 7rem;
      }
      .day{
        font-size: 2.4rem;
      }
    }
    .load{
      margin: 0 auto;
    }
    .off_text{
      margin-top: 30rem;
      h3{
        font-size: 4rem;
      }
    }
    .off_call{
      width: 15%;
      font-size: 1.4rem;
    }
  }
}