@import url('https://fonts.googleapis.com/css?family=Ubuntu&display=swap');

.se_main{
  height: 100vh;
  border: 1px solid red;
  box-sizing: border-box;
  background: #000;
  position: relative;

  .bg {
    z-index: 1;
    filter: blur(0px);
    
  }
  .loading-text {
    font-size: 5rem;
    color: #fff;
    z-index: 1;
    font-weight: bold;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .se_section{
    position: absolute;
    height: 92vh;
    width: 95%;
    left: 2%;
    top: 5%;
    border: 1px solid blue;
    display: flex;
    flex-direction: column;
    .se_head{
      display: flex;
      flex-direction: column;
      border: 1px solid green;
      width: 95%;
       margin: 3rem auto;


      .spread-out{
        font-size: 3rem;
        font-weight: bold;
        color: #fff;

        transition: .3s ease-in-out;
       &:hover{
        box-shadow: 0 0.5em 0.5em -0.4em #1973bc;
        transform: translateY(-0.25em);
        color: #1973bc;
       }
      }
      p{
        color: #ABB2BF;
        font-size: 1.2rem;
        font-weight: 600;
      }
      span{
        border: 3px solid #ABB2BF;
        width: 95%;
      }

    }
    .se_container{
      width: 95%;
      border: 1px solid green;
      height: 75vh;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      
      .se_content{
        position: relative;
        border: 1px solid blue;
        width: 50%;
        
        .se_con1{
          transition: .3s ease-in-out;
          filter: grayscale(100%);
          box-shadow: 10px 10px 10px black;
          position: absolute;
          left: 5%;
          top: 10%;
          width: 16vw;
          height: 21vh;
          background-color: #141414;
          border-radius: 30px;
          border: 1px solid red;
          &:hover{
            filter: grayscale(0);
            border: 1px solid #1973bc;
            
          }
          .se_title{
            display: flex;
            flex-direction: row;
            align-items: baseline;
            padding-top: 1rem;
            margin-left: 1rem;
            h3{
              font-size: 3rem;
              color: #fff;
              font-weight: bold;
              margin-right: 1rem;
            }
            p{
              color: #ABB2BF;
              font-weight: 600;
              font-size: 1rem;
            }
          }
          .se_skill{
            display: flex;
            flex-direction: row;
            padding: 1rem;
            span{
              color: #ABB2BF;
              font-weight: 600;
              padding: 0.6rem;
              border: 1px solid #ABB2BF;
              font-size: 1rem;
              margin-right: 1rem;
            }
            span:hover{
              animation: pulse 1s;
              box-shadow: 0 0 0 1.3em transparent;
              color: #fff;
              
              @keyframes pulse {
                0% {
                  box-shadow: 0 0 0 0 #ABB2BF;
                }
              }
            }
            
          }
          .se_emoji{
            position: absolute;
            bottom: 5%;
            right: 13%;
          }
        }

        .se_con2{
          transition: .3s ease-in-out;
          filter: grayscale(100%);
          box-shadow: 10px 10px 10px black;
          position: absolute;
          top: 5%;
          left: 62%;
          width: 16.5vw;
          height: 32vh;
          border: 1px solid red;
          background-color: #141414;
          border-radius: 30px;
          &:hover{
            filter: grayscale(0);
            border: 1px solid #1973bc;
          }
          .se_title{
            display: flex;
            flex-direction: column;
            padding-top: 1rem;
            margin-left: 1rem;
            h3{
              font-size: 3rem;
              color: #fff;
              font-weight: bold;
              margin-right: 1rem;
            }
            p{
              color: #ABB2BF;
              font-weight: 600;
              font-size: 0.9rem;
              padding: 0.5rem 0;
            }
            span{
              color: #ABB2BF;
              font-weight: 600;
              font-size: 0.9rem;
              border: 1px solid #ABB2BF;
              padding: 0.6rem;
              width: 17%;
            }
          }
          .se_skill{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            padding:1rem;
            span{
              color: #ABB2BF;
              font-weight: 600;
              padding: 0.6rem;
              border: 1px solid #ABB2BF;
              font-size: 1rem;
              margin-right: 1rem;
              &:hover{
                color: #fff;
                animation: pulse 1s;
                box-shadow: 0 0 0 1.3em transparent;
                color: #fff;
                
                @keyframes pulse {
                  0% {
                    box-shadow: 0 0 0 0 #ABB2BF;
                  }
                }
              }
            }
          }
          .se_emoji{
            position: absolute;
            bottom: 25%;
            right: 5%;
          }
        }
        .se_con3{
          transition: .3s ease-in-out;
          filter: grayscale(100%);
          box-shadow: 10px 10px 10px black;
          position: relative;
          top: 43%;
          left: 25%;
          border: 1px solid red;
          width: 16vw;
          height: 25vh;
          background-color: #141414;
          border-radius: 30px;
          &:hover{
            filter: grayscale(0);
            border: 1px solid #1973bc;
          }
          .se_title{
            display: flex;
            flex-direction: column;
            padding-top: 1rem;
            margin: 0 0.5rem;
            h3{
              font-size: 3rem;
              color: #fff;
              font-weight: bold;
              padding-bottom: 1.5rem;
  
            }
            p{
              color: #ABB2BF;
              font-weight: 600;
              font-size: 1rem;
              line-height: 1.5;
            }
            p:last-child{
              font-size: 0.8rem;
              margin-bottom: 1rem;
            }
          }
          .se_skill{
            padding: 1rem;
            box-sizing: border-box;
            span{
              color: #ABB2BF;
              font-weight: 600;
              padding: 0.6rem;
              border: 1px solid #ABB2BF;
              font-size: 1rem;
              margin-right: 1rem;
              transition: .3s ease-in-out;
              box-sizing: border-box;
              &:hover{
                box-shadow: inset -3.5em 0 0 2em #a972cb;
                color: #fff;
                border: none;
                border-radius: 4px;
                box-sizing: border-box;
              }
            }
          }
          .se_emoji{
            position: absolute;
            bottom: 5%;
            right: 10%;
          }
        }
        .se_con4{
          transition: .3s ease-in-out;
          filter: grayscale(100%);
          box-shadow: 10px 10px 10px black;
          position: absolute;
          left: 1%;
          bottom: 3%;
          width: 10vw;
          height: 17vh;
          border: 1px solid red;
          background-color: #141414;
          border-radius: 30px;
          background: url(../images/camera.png)no-repeat 50% 50%;
          &:hover{
            filter: grayscale(0);
            border: 1px solid #1973bc;
          }
        }
        .se_con5{
          transition: .3s ease-in-out;
          filter: grayscale(100%);
          box-shadow: 10px 10px 10px black;
          position: absolute;
          left: 63%;
          bottom: 7%;
          width: 15vw;
          height: 27vh;
          border: 1px solid red;
          background-color: #141414;
          border-radius: 30px;
          background: url(../images/camera.png)no-repeat 50% 50% / cover;
          &:hover{
            filter: grayscale(0);
            border: 1px solid #1973bc;
          }
        }
      }

    }
    .se_movie{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 39vw;
      height: 71vh;
      border: 1px solid red;
      background: url(../images/Front2.png)no-repeat 0 50% / contain;
      margin: 0 auto;
      // text-align: center; 
      // position: relative; 
      // padding-bottom: 56.25%; 
      // padding-top: 300px; 
      // height: 0; 
      // overflow: hidden; 
      // margin: 10px 10px 30px 10px;
      video{
        position: absolute;
        top: -10%;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0 auto;

      }
    }
  }
}
@media (max-width: 1068px) {
  .se_container{
    display: flex;
    flex-direction: column !important;
    height: 80vh !important;
    .se_content{
      width: 100% !important;
      height: 100% !important;
      .se_con1{
        width: 33vw !important;
        height: 13vh !important;
        border-radius: 20px !important;
        top: 5% !important;
        .se_emoji{
          right: 2% !important;
          bottom: 0 !important;
        }
      }
      .se_con2{
        width: 40vw !important;
        height: 25vh !important;
        border-radius: 20px !important;
        left: 51% !important;
        .se_emoji{
          bottom: 10% !important;
        }
      }
      .se_con3{
        width: 33vw !important;
        height: 22vh !important;
        border-radius: 20px !important;
        left: 9% !important;
      }
      .se_con4{
        display: none !important;
      }
      .se_con5{
        display: none !important;
      }

    }
    .se_movie{
      width: 100% !important;
      height: 100% !important;
    }
  }
}

@media (min-width:1069px) and (max-width:1399px) {
  .se_main{
    .se_section{
      .se_container{
        .se_content{
          .se_con2{
            .se_title{
              span{
                font-size: 0.7rem !important;
                width: 19% !important;
                padding: 0.6rem !important;
              }
            }
            .se_emoji{
              bottom: 10% !important;
            }
          }
        }
      }
    }
    .se_h3{
      font-size: 2rem !important;
    }
    .se_skill{
      span{
        margin-right: 0.3rem !important;
        padding: 0.5rem !important;
        font-size: 0.8rem !important;

      }
    }
  } 
} 

@media (min-width: 1400px) and (max-width:1894px) {
  .se_main{
    .se_section{
      .se_container{
        .se_content{
          .se_con2{
            .se_title{
              span{
                font-size: 0.8rem !important;
                width: 19% !important;
                padding: 0.7rem !important;
              }
            }
            .se_emoji{
              bottom: 3% !important;
            }
          }
        }
      }
    }
    .se_h3{
      font-size: 2rem !important;
    }
    .se_skill{
      span{
        margin-right: 0.4rem !important;
        padding: 0.6rem !important;
        font-size: 0.8rem !important;

      }
    }
  } 
}

