.kosdaq_main{
  height: 100vh;
  border: 1px solid red;
  box-sizing: border-box;
  background: #000;
  position: relative;
  .kosdaq_section{
    display: flex;
    flex-direction: row;
    position: absolute;
    height: 92vh;
    width: 95%;
    left: 2%;
    top: 5%;
    border: 1px solid blue;
    .kosdaq_movie{
      width: 43vw;
      height: 90vh;
      border: 1px solid red;
      .kosdaq_phone{

      }
      .kosdaq_mac{

      }
      .kosdaq_desk{

      }
    }
    .kosdaq_container{
      width: 50vw;
      height: 90vh;
      border: 1px solid green;
      margin: 0 auto;
      .kosdaq_search{
        width: 40vw;
        height: 5vh;
        background-color: rgba(52, 52, 52, 0.7);
        margin: 1rem auto;
        border-radius: 12px;
        padding: 0 1rem;
        p{
          font-size: 1rem;
          color: #767574;
          text-align: center;

        }
      }
      .kosdaq_titles{
        display: flex;
        flex-direction: column;
        width: 40vw;
        height: 13vh;
        border: 1px solid red;
        margin: 1rem auto;
        background-color: #000;
        border-radius: 20px;
        padding: 1rem;
        p{
          color: #D44E58;
          font-size: 1rem; 
          font-weight: 600; 
        }
        .glitch{
          color: #fff;
          font-size: 3rem;
          font-weight: bold;
          mix-blend-mode: lighten;
          position: relative;
          &:before,
          &:after {
              content: attr(data-text);
              position: absolute;
              
              width: 100%;
              background: black;
              clip: rect(0, 0, 0, 0);
          }
          
          &:before {
              left: -1px;
              text-shadow: 1px 0 rgba(red,0.7);
          }
        
          &:after {
              left: 1px;
              text-shadow: -1px 0 rgba(blue,0.7);
          }
        
          &:hover {
            &:before {
              text-shadow: 4px 0 rgba(red,0.7);
              animation: glitch-loop-1 0.8s infinite ease-in-out alternate-reverse;
            }
            &:after {
              text-shadow: -5px 0 rgba(blue,0.7);
              animation: glitch-loop-2 0.8s infinite ease-in-out alternate-reverse;
            }
          }

          b{
            color: #5ECCA3;
            font-size: 1rem;
          }
        }
        @keyframes glitch-loop-1 {
          0% { clip: rect(36px, 9999px, 9px, 0) }
          25% { clip: rect(25px, 9999px, 99px, 0) }
          50% { clip: rect(50px, 9999px, 102px, 0) }
          75% { clip: rect(30px, 9999px, 92px, 0) }
          100% { clip: rect(200px, 9999px, 98px, 0) }
        }
        
        @keyframes glitch-loop-2 {
          0% { top: -1px; left: 1px; clip: rect(65px, 9999px, 119px, 0) }
          25% { top: -6px; left: 4px; clip: rect(79px, 9999px, 19px, 0) }
          50% { top: -3px; left: 2px; clip: rect(68px, 9999px, 11px, 0) }
          75% { top: 0px; left: -4px; clip: rect(95px, 9999px, 53px, 0) }
          100% { top: -1px; left: -1px; clip: rect(31px, 9999px, 149px, 0) }
        }
        span{
          font-size: 0.9rem;
          font-weight: bold;
          color: #ABB2BF ;
        }
      }
      .kosdaq_skill{
        display: flex;
        flex-direction: column;
        width: 40vw;
        height: 18vh;
        border: 1px solid red;
        margin: 0 auto;
        padding: 1rem;
        border-radius: 20px;
        background-color: #000;
        .kosdaq_built{
          display: flex;
          flex-direction: row;
          align-items: left;
          padding-bottom: 0.5rem;
          h2{
            color: #fff;
            font-size: 3rem;
            font-weight: bold;
          }
          .kosdaq_date{
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            color: #ABB2BF;
            margin-left: 1rem;
          }
        }
     
      }
      .kosdaq_index{
        display: flex;
        flex-direction: column;
        
        p:nth-child(2){
          color: #fff;
          font-size: 1rem;
          font-weight: bold;
          margin-right: 1rem;
        }
        p:last-child{
          color: #5ECCA3;
          font-size: 0.8rem;
          
        }
        .circle{
          border: 1px solid #7197F8;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          text-align: center;
          color: #7197F8;
          font-size: 0.7rem;
          margin-right: 1rem;
          
        }

        .kosdaq_html{
          display: flex;
          flex-direction: row;
          padding-bottom: 1rem;
        }
        .kosdaq_css{
          display: flex;
          flex-direction: row;
          padding-bottom: 1rem;
        }
        .kosdaq_js{
          display: flex;
          flex-direction: row;
          padding-bottom: 1rem;
        }  
      }
      .kosdaq_layout{
        width: 42vw;
        height: 40vh;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 1rem auto;


        .kosdaq_info{
          width: 18vw;
          height: 35vh;
          border: 1px solid red;
          padding: 1rem;
          background-color: #000;
          border-radius: 20px;
        }
        .kosdaq_info2{
          width: 18vw;
          height: 35vh;
          border: 1px solid green;
          padding: 1rem;
          background-color: #000;
          border-radius: 20px;
          
  
        }
      }
      .kosdaq_title{
        display: flex;
        flex-direction: row;
        align-items: center;
        align-self: center;
        span{
          border: 5px solid #6CD06C;
          border-radius: 50%;
          width: 40px;
          height: 40px;
          margin-right: 1rem;
          position: relative;
          svg{
            margin: 0.3rem auto;
            display: block;
      

            
          }
        }
        p{
          color: #fff;
          font-size: 2.5rem;
          font-weight: bold;
          align-self: center;
          
        }
      }
      .kosdaq_content{
        display: flex;
        flex-direction: column;
        border: 1px solid red;
        height: 27vh;
        h3{
          color: #fff;
          font-weight: bold;
          font-size: 1.8rem;
          padding: 1rem 0;
        }
        p{
          color: #ABB2BF;
          font-size: 0.9rem;
        }
      }
      .kosdaq_footer{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border: 1px solid red;

        p{
          color: #ABB2BF;
        }
        span{
          color: #fff;
          font-weight: bold;
        }
      }

    }
  }
}
@media (max-width: 768px){
  .kosdaq_main{
    .kosdaq_section{
      flex-direction: column-reverse; 
      .kosdaq_movie{
        width: 95%;
        height: 41vh;
        border: 1px solid orange;
        margin: 0 auto;
      }
      .kosdaq_container{
        width: 95%;
        height: 54vh;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        
        .kosdaq_search{
          width: 95%;
          height: 4vh; 
          margin-top: 2rem; 
        }
        .kosdaq_titles{
          height: 18vh;
          margin: 0;
          width: 45%;
          .glitch{
            font-size: 2rem;
            margin-bottom: 2rem;
          }
          span{
            font-size: 1.4rem;
          }
        }
        .kosdaq_skill{
          width: 45%;
          margin: 0;
          .kosdaq_built{
            h2{
              font-size: 2rem;
            }
          }
          .kosdaq_index{
            
          }
         
        }
        .kosdaq_layout{
          width: 100%;
          display: flex;
          flex-direction: row;
          .kosdaq_info{
            width: 45%;
            height: 60%;
          }
          .kosdaq_info2{
            width: 45%;
            height: 60%;
            
          }  
          .kosdaq_title{

          }
          .kosdaq_content{
            height: 16vh;
          }
          .kosdaq_footer{
      
          }
        }
      }
    }
  }

}
@media (min-width:769px) and (max-width:1399px) {
  .kosdaq_skill{
    .kosdaq_built{
      p:nth-child(2){
        left: 64% !important;
        top: 29% !important;
      }
      p:nth-child(3){
        top: 30.5% !important;
        left: 64% !important;
      }
    }
  }
}
@media (min-width:1400px) and (max-width:1880px) {

