.call_main{
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background: #000;
  position: relative;

@keyframes carousel-animate {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(200%) scale(0.7);
  }
  3%, 14.2857142857% {
    visibility: visible;
    opacity: 0.8;
    transform: translateX(100%) scale(0.9);
  }
  17.2857142857%, 28.5714285714% {
    visibility: visible;
    opacity: 1;
    transform: translateX(0) scale(1);
  }
  31.5714285714%, 42.8571428571% {
    visibility: visible;
    opacity: 0.8;
    transform: translateX(-100%) scale(0.9);
  }
  45.8571428571% {
    visibility: visible;
    opacity: 0;
    transform: translateX(-200%) scale(0.9);
  }
  100% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(-200%) scale(0.7);
  }
}
  .container {
    position: relative;
    z-index: 10;
    display: grid;
    place-items: center;
    height: 100vh;
    border: 1px solid green;
    h2{
      position: absolute;
      border: 1px solid red;
      font-size: 5rem;
      font-weight: bold;
      color: #fff;
      left: 5%;
      top: 10%;
      
    }
      .carousel {
        pointer-events: none;
        position: absolute;
        padding-block-start: 67px;
        padding-block-end: 24px;
        perspective: 100px;
        width: 100%;
        border: 1px solid blue;

        .carousel__wrapper {
          position: relative;
          display: flex;
          justify-content: center;
          height: 61.2vh;
          border: 1px solid red;
          .item{
            border: 3px solid green;
            position: absolute;
            max-width: 418px;
            height: 100%;
            margin-inline:32px;
            opacity: 0;
            will-change: transform, opacity;
            animation: carousel-animate 27s cubic-bezier(0.37, 0, 0.63, 1) infinite;
            
            .skill{
              border: 1px solid #ABB2BF;
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
              height: 4vh;
              text-indent: 10px;
              color: #ABB2BF;
              font-weight: 400;
              p{
                margin-right: 3px;
                font-size: 1rem;
              }
            }
            .box1{
              display: flex;
              flex-direction: column;
              height: 17vh;
              justify-content: center;
              h3{
                color: #fff;
                font-weight: 500;
                font-size: 1.5rem;
                padding: 0.8rem;
                text-align: center;
                margin-top: 2rem;
              }
              p{
                color: #ABB2BF;
          
                text-align: center;
          
              }
            }
            .box2{
              display: flex;
              flex-direction: row;
              justify-content: center;
              padding: 1rem;
              margin-top: 1rem;
              cursor: pointer;
                span:first-child{
                  color: #fff;
                  border: 2px solid #C778DD;
                  padding: 0.6rem 2rem;
                  margin-right: 1rem;
                }
                span:last-child{
                  border: 2px solid #ABB2BF;
                  color: #ABB2BF;
                  padding: 0.6rem 2rem;
                }
            }
              .con1{
              cursor: pointer;
              display: flex;
              flex-direction: column;
              z-index: 10;
              border: 1px solid #ABB2BF;
              height: 100%;
              width: 100%;
              border-radius: 15px;
              .picture{
                background: url(../images/main_video_thum2.jpg)no-repeat 0 0 / cover;
                width: 21vw;
                height: 32vh;
                border-radius: 15px 15px 0 0;
              }
          
              }
            &:nth-child(1) {
          
              animation-delay: calc(3.8571428571s * -1);
              }
              
              .con2{
                display: flex;
                flex-direction: column;
                
                z-index: 10;
                border: 1px solid #ABB2BF;
                height: 100%;
                width: 100%;
