                }
              }
              &:last-child {
          
                  animation-delay: calc(-3.8571428571s * 2);
              }
          
              img {
                max-width: 100%;
                  border: 1px solid orange;
                object-fit: cover;
                height: 100%;
              }
            }
      }  
    }
  }
}

